.buttonSecondary {
    background-color:transparent;
    border: 3px solid #171a20;
    width:100%;
    padding: 10px 40px;
    border-radius: 50px;
    text-transform: uppercase;
    color:#171a20;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    outline:none;

}

.buttonSecondary:hover {
    background-color:#171a20;
    color:white;
}